<template>
  <div class="container">
    <section v-if="courseDetails.data">
      <!-- 主页介绍 -->
      <div class="tutor space">
        <el-row :gutter="10">
          <el-col :xs="24" :sm="24" :md="10" :lg="10" :xl="10">
            <div class="avatar">
              <img :src="courseDetails.data.info.surface"/>
            </div>
          </el-col>
          <el-col :xs="24" :sm="24" :md="14" :lg="14" :xl="14" class="df dfdir dfjbw courseInfo">
            <div class="basicInfo">
              <p class="name">{{courseDetails.data.info.name}}</p>
              <p class="tags">
                        <span
                          v-for="(t,i) in courseDetails.data.times"
                          :key="t+i">{{t.view}}</span>
              </p>
            </div>
            <div class="contact df dfjbw dfc">
              <div class="touch df dfc">
                <p class="price"><span>￥</span>{{courseDetails.data.info.price}}</p>
                <p class="learn">累计<span>{{courseDetails.data.info.bought_cnt}}</span>人报名</p>
              </div>
              <span class="enroll" @click="attendIn">报名</span>
            </div>
          </el-col>
        </el-row>
      </div>

      <div class="modular space">
        <el-row :gutter="20">
          <el-col :xs="24" :sm="24" :md="18" :lg="18" :xl="18">
            <div class="vessels">
              <Modular :modeInfo="baseInfo" v-if="baseInfo.content" type="1"/>
              <Modular :modeInfo="teacherInfo" v-if="teacherInfo.list&&teacherInfo.list.length>0" type="4"/>
              <Modular :modeInfo="suggestCrowdInfo" v-if="suggestCrowdInfo.list&&suggestCrowdInfo.list.length>0"
                       type="5"/>
              <Modular :modeInfo="evaluateInfo" v-if="evaluateInfo.list&&evaluateInfo.list.length>0" type="6"/>


              <Modular :modeInfo="syllabusInfo" v-if="syllabusInfo.content" type="1"/>
              <Modular :modeInfo="reportInfo" v-if="reportInfo.content" type="1"/>
              <Modular :modeInfo="tipsInfo" v-if="tipsInfo.content" type="1"/>
            </div>
            <!-- <div class="position"></div> -->
          </el-col>
          <!--<el-col :xs="24" :sm="24" :md="6" :lg="6" :xl="6">
              <h3 class="title">推荐老师</h3>
              <div class="tList">
                  <div class="character">
                      <p class="photoShow" style='backgroundImage:url("https://sslfile.ichazuo.cn/1480920120bb1443cc31d7396bf73e7858cea114e1.jpg")'></p>
                      <div class="desc">
                          <p class="name">
                              倪建伟
                              <span v-if="true">销售类</span></p>
                          <div class="ellipsis2 info">
                              <p>服务过华为、海尔、腾讯、 苏宁、中国移动等100+企业</p>
                          </div>
                      </div>
                  </div>
              </div>
          </el-col> -->
        </el-row>
      </div>
    </section>
  </div>
</template>
<script>
  import {mapState, mapActions} from 'vuex'
  import merge from 'webpack-merge'
  import Modular from '../../../components/Modular'

  export default {
    data() {
      return {
        navIndex: 0,
        baseInfo: {},
        teacherInfo: {},
        suggestCrowdInfo: {},
        evaluateInfo: {},

        syllabusInfo: {},
        reportInfo: {},
        tipsInfo: {}
      }
    },
    computed: {
      ...mapState({
        courseDetails: state => state.course.courseDetails
      }),
    },
    created() {
      const {id} = this.$route.params;
      document.title = `讲师详情 - 插座学院`;
      // 获取课程详细信息
      this.getCourseDetails(id);

    },
    methods: {
      ...mapActions({
        getCourseInfo: 'course/getCourseInfo'
      }),

      async getCourseDetails(id){
        const res = await this.getCourseInfo({catalogId: id});
        const {comments = [], info = {}, studentLabels = [], teachers = [], times} = res.data;

        this.baseInfo = {
          title: "课程概述",
          content: info.catalog_description
        }
        this.teacherInfo = {
          title: "授课讲师",
          list: teachers
        }
        this.suggestCrowdInfo = {
          title: "建议学习人群",
          list: studentLabels
        }
        this.evaluateInfo = {
          title: "学员评价",
          list: comments
        }
        this.syllabusInfo = {
          title: "课程大纲",
          content: info.catalog_outline
        }
        this.reportInfo = {
          title: "报名须知",
          content: info.catalog_notes
        }
        this.tipsInfo = {
          title: "温馨提示",
          content: info.catalog_img
        }
      },

      // 报名
      attendIn(){
        console.log("报名");
        console.log(this.courseDetails);
      },
      /**
       * 路由跳转
       * @param path 路由地址
       */
      toPath(path) {
        this.$router.push(path)
      },
    },
    components: {
      Modular
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .container {
    width: 75%;
    margin: 0 auto;
    overflow: hidden;
    min-height: 100vh;
    margin-bottom: -295px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 380px;
    }

    .space {
      margin-top: 24px;

    }

    .tutor {
      background-color: #fff;
      padding: 40px;
      .avatar {
        position: relative;
        width: 370px;
        min-height: 208px;
        background-image: linear-gradient(to bottom, #F6EADE 0%, #F5DEC9 40%, #866F5B 100%);
        margin: 0 auto 24px;
        text-align: center;
        img {
          max-width: 100%;
          vertical-align: middle;
        }
      }
      .courseInfo {
        min-height: 208px;
        .basicInfo {
          .name {
            font-size: 40px;
            color: #333;
            margin-bottom: 20px;
          }
          .tags {
            overflow: hidden;
            margin: 14px -8px;
            span {
              float: left;
              margin: 8px;
              padding: 0 16px;
              line-height: 36px;
              font-size: 14px;
              color: #06609A;
              border: 1px solid #E6EFF4;
              border-radius: 18px;
            }
          }
        }

        .contact {
          .enroll {
            width: 188px;
            line-height: 64px;
            text-align: center;
            background: #06609A;
            border-radius: 32px;
            color: #fff;
            font-size: 24px;
          }

          .touch {
            font-size: 16px;
            line-height: 1.6;
            color: #444;
            .price {
              font-size: 24px;
              color: #E2AD87;
              font-weight: bold;
              margin-right: 10px;
              span {
                font-size: 14px;
              }
            }
            .learn {
              span {
                font-size: 18px;
                color: #06609A;
              }
            }
          }
        }
      }
    }

    .modular {
      .vessels {
        background: #fff;
       // padding-bottom: 80px;
      }
      // .position {
      //   background: #fff;
      //   height: 80px;
      // }

      .title {
        font-size: 24px;
        color: #666;
        margin: 16px 0 12px;
      }
      .tList {
        .character {
          padding: 20px;
          background: #fff;
          box-shadow: 3px 4px 8px #f7f7f7;
          position: relative;
          overflow: hidden;
          cursor: pointer;
          border-radius: 6px;
          .photoShow {
            width: 100%;
            height: 218px;
            display: block;
            border-radius: 8px;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
          }
          .desc {
            width: 100%;
            background: #fff;
            padding: 24px 20px;
            box-sizing: border-box;
            .name {
              font-size: 24px;
              color: #06609A;
              span {
                padding: 2px 6px;
                margin-left: 8px;
                background: #E2AD87;
                color: #80624C;
                border-radius: 4px;
                font-size: 12px;
              }
            }
            .info {
              font-size: 14px;
              color: #666;
              line-height: 2;
              margin-top: 8px;
              .goodAt {
                margin-top: 10px;
              }
            }
          }
        }
      }

    }
  }

  .page {
    padding: 32px 0;
    text-align: center;
  }

  @media screen and (min-width: 200px) and (max-width: 768px) {
    .container {
      box-sizing: border-box;
      display: flex;
      width: 100%;
      overflow: hidden;
      flex-direction: column;
    }

    .sidebar {
      box-sizing: border-box;
      width: auto;
      flex: 1;
      margin-left: 0;
      margin-top: 24px;
    }
  }
</style>



